import React, { useState } from 'react';
import { API } from '../../API';
import swal from 'sweetalert'

function Registration() {

const [name,setName]=useState("")
const [mobile,setMobile]=useState("")
const [lastName,setlastName]=useState("")
const [password,setPassword]=useState("")
const [email,setEmail]=useState("")
const [confermPassword,setConPassword]=useState("")

const Registration_APi = async () => {
	try {
	if(password != confermPassword){
		alert("Conferm Password is not match")
	}
	else{
	  let responce = await API.post('/registration', {
		"f_name":name,
		"l_name":lastName,
		"password":password,
		"email":email,
		"mobile":mobile
	  })
	  responce = responce.data.data
	  console.log('res', responce)

	  if(responce.result=="Successfull"){
		swal({
			title: 'Success..!',
			text: responce.return_result,
			icon: 'success',
			button: 'OK',
		  }).then((okay) => {
			if (okay) {
			  window.location.href = './Dashboard'
			}
		  })
		}else {
		  swal({
			title: "Registration Error..!!",
			text: responce.result,
			icon: "error",
			button: "OK",
		  })
		}
	}
	} catch (e) {
	  console.log('Something Error', e)
	}
  }


//   useEffect(() => {
// 	Binary_Income()
//   }, [])


  return (
    <div>

<div class="container">
		<div class="row">
			<div class="col-md-6">
				<div class="login_form registerForm">
				    <h1>SIGN UP</h1>
				    <p>Enter your personal details to begin your journey with us.</p>
				    <form>
				    	<div class="row">
				    		<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="firstName">First Name</label>
							        <input type="text" class="form-control" onChange={(e)=>setName(e.target.value)} placeholder="First Name" name="First name" required />
							    </div>
							</div>
							
				    		<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="LastName">Last Name</label>
							        <input type="text" class="form-control" onChange={(e)=>setlastName(e.target.value)} placeholder="Last Name" name="Last name" required />
							    </div>
				    		</div>
							<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="mobile">Mobile No.</label>
							        <input type="text" class="form-control" onChange={(e)=>setMobile(e.target.value)} placeholder="Mobile No." name="Last name" required />
							    </div>
				    		</div>
				    		<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="email">Email Id</label>
							        <input type="email" class="form-control" onChange={(e)=>setEmail(e.target.value)} placeholder="Email Id" name="email" required />
							    </div>
				    		</div>
				    		<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="password">Password</label>
							        <input type="password" class="form-control" onChange={(e)=>setPassword(e.target.value)} placeholder="Password" name="pswd" required />
							    </div>
				    		</div>
				    		<div class="col-md-6">
				    			<div class="form-group">
				    				<label for="confirmPassword">Confirm Password</label>
							        <input type="password" class="form-control" onChange={(e)=>setConPassword(e.target.value)} placeholder="Confirm Password" name="pswd" />
							    </div>
				    		</div>
				    	</div>
			            <div class="login_btn">
			            	<button type="button" onClick={()=>Registration_APi()} class="btn button btn-block">Sign Up</button>
			            </div>
			            <p>Already have an account  <a href="/">Sign In</a></p>
			        </form>	
				</div>
			</div>
			<div class="col-md-6">
				<div class="login_img">
					<img src="assets/images/login_img.png" />
				</div>
			</div>
		</div>
	</div>
	

    </div>
  )
}

export default Registration